import React, { useEffect, useState } from "react";
import styles from "./styles.module.css";
import axios from "../../axios";
import Button from "../Button";
import Table from "../Table";
import { Draggable } from "react-beautiful-dnd";
function Notifikasi(props) {
  // const [linkTemp, setLinkTemp] = useState([]);
  const [Notification, setNotification] = useState([]);
  const [listUsers, setListUsers] = useState([]);
  const [panelCahnge, setPanelChange] = useState("main");
  const [title, setTitle] = useState("");
  const [durationNotif, setDurationNotif] = useState("");
  const [selectUsers, setSelectUsers] = useState("");
  const [deskrispi, setDeskripsi] = useState("");
  const [linkImg, setLinkImg] = useState("");
  const [fileName, setFileName] = useState("");
  const [img, setImg] = useState(new FormData());
  const [refreshRender, setRefreshRender] = useState(false);
  const [showTambahData, setShowTambahData] = useState(false);
  const [editButtonPressed, setEditButtonPressed] = useState(false);
  const [addButtonPressed, setAddButtonPressed] = useState(false);
  const [dataSelectedNow, setDataSelectedNow] = useState(null);

  const base_url = "https://fisikamu.space";

  useEffect(() => {
    async function fetchdata() {
      getNotification();
    }

    fetchdata();

    if (refreshRender) {
      getNotification();
      setRefreshRender(false);
    }
    if (addButtonPressed) {
      setTitle("");
      setLinkImg("");
      setAddButtonPressed(false);
    }
  }, [refreshRender, addButtonPressed, editButtonPressed]);

  /// action function

  const getNotification = () => {
    axios.get("/notification").then(function (response) {
      setNotification(response.data);
    });
  };
  const loadUsersList = () => {
    axios.get("/users_api").then(function (response) {
      setListUsers(response.data);
      // console.log(response.data.filter((e)=>e.id===item.id_users));
    });
  };
  const selectUsersChanged = (e) => {
    setSelectUsers(e.target.value);
  };

  const onImageChange = (event) => {
    let files = event.target.files;
    const extLength = files[0]["name"].split(".").length;
    const dateCode = Date.now();
    const ext = files[0]["name"].split(".")[extLength - 1];
    const file_name = "slide_" + dateCode + "." + ext;
    setFileName(file_name);
    if (FileReader && files && files.length) {
      img.append("name", "slide_" + dateCode);
      img.append("name_before", "slide_" + dateCode);
      img.append("file", files[0]);
    }
  };

  const kembali = () => {
    setPanelChange("main");
    setAddButtonPressed(true);
    setEditButtonPressed(false);
    setShowTambahData(false);
  };

  const konfirmasi = (item) => {
    const data = {
      id: item.id,
      id_user: item.id_user,
      id_paket: item.id_paket,
      payment_type: item.payment_type,
      jumlah: item.jumlah,
      status: "Aktif",
      description: item.description,
    };
    // console.log(data);

    axios.put("/notification", data).then(function (response) {
      // console.log(response);
      if (response.status == 200) {
        setShowTambahData(false);
        setRefreshRender(true);
      }
    });
  };

  const save = () => {
    if (title !== null && deskrispi !== "" && selectUsers !== "") {
      const data = {
        title: title,
        description: deskrispi,
        id_users: selectUsers,
      };
      // console.log(data);
      axios.post("/notification", data).then(function (response) {
        console.log(response);
      });

      setRefreshRender(true);
      setShowTambahData(false);
    } else {
      alert("Isi Semua Data");
    }
  };

  const hapusData = (dataTemp) => {
    let data_delete = require("querystring");
    data_delete = data_delete.stringify({
      id: dataTemp.id,
    });

    axios
      .delete("/notification", {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
        data: data_delete,
      })
      .then(function (response) {
        console.log(response);
        if (response.status === 201) {
          setPanelChange("main");
          setRefreshRender(true);
        }
      });
  };

  const editData = (data) => {
    setEditButtonPressed(true);
    setTitle(data.title);
    setLinkImg(data.img);
    setDataSelectedNow(data);
    setShowTambahData(true);
  };

  const dataEdited = () => {
    console.log("ASasf");
    const data = {
      id: dataSelectedNow.id,
      title: title,
      img: linkImg,
    };

    axios.put("/slide_content", data).then(function (response) {
      console.log(response);
      if (response.status == 200) {
        setTitle("");
        setLinkImg("");
        setShowTambahData(false);
        setRefreshRender(true);
      }
    });
  };

  const changeTipe = (dataTemp) => {
    const data = {
      id: dataTemp.id,
      id_users: dataTemp.id_users,
      title: dataTemp.title,
      description: dataTemp.description,
      status: dataTemp.status,
      send_status: dataTemp.send_status,
      tipe: dataTemp.tipe == "terbaru" ? "penting" : "terbaru",

      // id_bab: selectbabSoal,
    };

    console.log(data);
    axios.put("/notification", data).then(function (response) {
      console.log(response);
      if (response.status == 200) {
        setRefreshRender(true);
        // setPanelChange("main");
      }
    });
  };

  // panel components

  const mainPanel = (
    <>
      {/* <h1></h1> */}
      <Table
        dropID={"drop-1"}
        thList={["no", "Judul", "Deskripsi", "Status", "User", "Action"]}
        tdList={Notification.map((item, index) => (
          <tr key={item.id}>
            <td>{index + 1}</td>
            <td>{item.title}</td>
            <td>{item.description}</td>
            <td>{item.tipe}</td>
            <td>{item.username}</td>
            {/* <td>{item.duration} hari</td> */}
            {/* <td><a href="#0" onClick={()=>changeTipe(item)}>{item.tipe == "terbaru"?"penting":"terbaru"}</a></td> */}
            <td>
              <a href='#0' onClick={() => hapusData(item)}>
                Hapus
              </a>
            </td>
          </tr>
        ))}
      />
      <br />
      <Button
        link=''
        title='Buat Notifikani'
        onClick={() => {
          setShowTambahData(true);
          loadUsersList();
        }}
      />
    </>
  );

  const panelTambahData = (
    <div style={{ padding: "8px" }}>
      <h1>Buat Notifikasi</h1>
      <input
        value={title}
        type='text'
        placeholder='Judul'
        onChange={(e) => {
          setTitle(e.target.value);
        }}
      />
      <br />
      <br />
      {/* <input value={title} type="text" placeholder="Deskripsi" onChange={(e) => { setDeskripsi(e.target.value) }} /> */}
      <textarea
        value={deskrispi}
        onChange={(e) => {
          setDeskripsi(e.target.value);
        }}
        name='deskrispi'
        id='deskrispi'
        cols='30'
        rows='10'
      ></textarea>
      <br />
      <span>Kirim ke :</span>
      <br />
      <select value={selectUsers} onChange={selectUsersChanged}>
        <option value='' disabled></option>
        {listUsers.map((item) => (
          <option key={item.id} value={item.id}>
            {item.username}
          </option>
        ))}
      </select>
      <br />
      {/* <input value={durationNotif} type="number" placeholder="Durasi (Dalam Hari)" onChange={(e) => { setDurationNotif(e.target.value) }} />
            <br/> */}
      <br />

      <Button
        link=''
        title='Submit'
        onClick={editButtonPressed ? () => dataEdited() : () => save()}
      />
      <Button link='' title='Kembali' onClick={() => kembali()} />
    </div>
  );

  return (
    <>
      <div className={styles.container}>
        {panelCahnge === "main" ? mainPanel : null}
      </div>
      {showTambahData ? (
        <div className={styles.tambah_soal}>
          <div className={styles.layer1}>{panelTambahData}</div>
        </div>
      ) : null}
    </>
  );
}

export default Notifikasi;
